.projects-list-root {
  grid-template-rows: max-content !important;
}

.project-item-root {
  height: auto !important;
  min-width: 200px;
}

.project-item {
  display: flex;
  flex-direction: column;
}

.project-item img {
  height: 14vmax;
  width: 95%;
  align-self: center;
  object-fit: cover;
  border-radius: 10px;
  border: 2px solid #ccfcfc;
  margin: 1vmax 2vmax;
}

.project-item p {
  padding: 1vmax;
}

.other-item {
  height: auto;
  width: auto;
  display: flex;
}

.other-item .scar-logo {
  height: 15vmax;
  width: 15vmax;
  border: none;
  text-align: center;
  align-self: center;
}
